// Set loading to true for next render
export const clearMovies = () => {
  return {
    type: TYPES.FETCH_MOVIES_LOADING,
  };
};

// Get single movie
export const getMovie = (type, id) => async dispatch => {
  try {
    dispatch({ type: TYPES.FETCH_MOVIE_LOADING });
    const res = await tmdbAPI.get(`/${type}/${id}`, {
      params: {
        append_to_response: 'videos',
      },
    });
    await dispatch({
      type: TYPES.FETCH_MOVIE,
      payload: prepareMovie(type, res.data),
    });
    await dispatch(getCredits(type));
    dispatch({ type: TYPES.FETCH_MOVIE_FINISHED });
  } catch (err) {
    dispatch({ type: TYPES.INSERT_ERROR, payload: err.response });
    history.push(process.env.PUBLIC_URL + '/error');
  }
};

// Set loading to true for next render
export const clearMovie = () => {
  return {
    type: TYPES.FETCH_MOVIE_LOADING,
  };
};

// Get credits of single movie
export const getCredits = (type) => async (dispatch, getState) => {
  const { id } = getState().movie;

  try {
    const res = await tmdbAPI.get(`/${type}/${id}/credits`);
    dispatch({
      type: TYPES.FETCH_CAST,
      payload: res.data.cast,
    });
  } catch (err) {
    dispatch({ type: TYPES.INSERT_ERROR, payload: err.response });
    history.push(process.env.PUBLIC_URL + '/error');
  }
};

