import React, { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { animateScroll as scroll } from 'react-scroll';

import Header from '../components/Header';

import { setSelectedMenu } from '../actions';
import MoviesList from '../components/MoviesList';

import { AppContext } from '../context';
import { getStorageValue } from '../hooks';
import history from '../history';

const Wrapper = styled.div`
  display: flex;
  width: 100%;
  flex-direction: column;
`;

// Discover Component
const Bookmarks = ({
  geral,
  match,
  location,
  setSelectedMenu,
  movies,
}) => {
  const { secure_base_url } = geral.base.images;

  const [ count, setCount ] = React.useState(0);
  const { setLastOpened } = React.useContext(AppContext);

  useEffect(() => {
    setSelectedMenu();
    setLastOpened({ type: 'bookmarks' });
    return () => {
      setLastOpened(null);
    };
  }, []);

  React.useEffect(() => {
    const interval = setInterval(() => {
      setCount(0);
    }, 5000);
    return () => {
      clearInterval(interval);
    };
  }, []);

  return (
    <Wrapper>
      <Helmet>
        <meta charSet="utf-8" />
        <title>{'Bookmarks'}</title>
      </Helmet>
      <div>
        <Header title={'Bookmarks'} subtitle={'favorite'} onClick={() => {
          if (count > 3) {
            history.push(process.env.PUBLIC_URL + '/plugins');
          } else {
            setCount(count + 1);
          }
        }} />
      </div>
      <MoviesList movies={movies} baseUrl={secure_base_url} hideVote={true} hidePagination={true} />
    </Wrapper>
  );
};

// Map State to Component Props
const mapStateToProps = ({ geral, movies }) => {
  const bookmarks = getStorageValue('bookmarks', {});
  return { geral, movies: {
    results: Object.values(bookmarks),
  } };
};

export default connect(
  mapStateToProps,
  { setSelectedMenu }
)(Bookmarks);
