import React, { Fragment } from 'react';
import { Helmet } from 'react-helmet';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';

import store from './store';
import { ThemeProvider } from 'styled-components';
import theme from './utils/theme';
import GlobalStyle from './utils/globals';
import App from './containers/App';

import { AppContext } from './context';
import { useLocalStorage } from './hooks';

import '../node_modules/react-modal-video/scss/modal-video.scss';
import '../node_modules/slick-carousel/slick/slick.css';
import '../node_modules/slick-carousel/slick/slick-theme.css';
import './styles.css';

import pluginsLoader from './plugins'; // register plugins

const MenyProvider = ({ children }) => {
    const [ type, setType ] = useLocalStorage('menu_type', 'movie');
    const [ lastOpened, setLastOpened ] = useLocalStorage('last_opened', null);

    return (
        <AppContext.Provider value={{ type, setType, menuTypes: ['movie', 'tv'], lastOpened, setLastOpened }}>
            {children}
        </AppContext.Provider>
    );
};

pluginsLoader.then(() => {
  document.querySelector('#loader').style.display = 'none';

  ReactDOM.render(
    <Provider store={store}>
      <ThemeProvider theme={theme}>
          <MenyProvider>
            <Fragment>
              <Helmet>
                <title>TMDB APP</title>
                <meta
                  name="description"
                  content="A Movie Library where you can check all your favorite movies, as well as the cast of it, and so mucnh more!"
                />
              </Helmet>
              <App />
              <GlobalStyle />
            </Fragment>
        </MenyProvider>
      </ThemeProvider>
    </Provider>,
    document.querySelector('#root')
  );
});
