import React from 'react';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import Header from '../components/Header';
import styled from 'styled-components';
import { animateScroll as scroll } from 'react-scroll';

import { useLocalStorage } from '../hooks';

const Wrapper = styled.div`
    display: flex;
    width: 100%;
    flex-direction: column;
`;

const Plugins = ({ match, location }) => {
    const [plugins, setPlugins] = useLocalStorage('plugins', '');

    React.useEffect(() => {
        scroll.scrollToTop({
            smooth: true,
        });
    }, []);

    return (
        <Wrapper>
            <Helmet>
                <title>{`Plugins`}</title>
            </Helmet>
            <Header title={'Plugins'} />
            <form>
                <textarea
                    value={plugins}
                    style={{ width: '100%', height: '300px', padding: '10px' }}
                    onChange={(e) => setPlugins(e.target.value)}
                    placeholder="http://127.0.0.1:8080/plugins/name.js"
                />
            </form>
        </Wrapper>
    );
};

// Map State to Component Props
const mapStateToProps = ({}) => {
    return {};
};

export default connect(
    mapStateToProps,
    {}
)(Plugins);
