import React from 'react';
import Modal from 'react-modal';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

import Button from './components/Button';
import Loader from './components/Loader';
import MenuItem from './components/MenuItem';

import { getStorageValue, useLocalStorage } from './hooks';

import plugin from 'js-plugin';

Modal.setAppElement('#root');

const loadScript = (url, async = true) => new Promise((resolve, reject) => {
    const script = document.createElement('script');
    script.src = url;
    script.async = async;
    script.onload = () => resolve();
    script.onerror = reject;

    document.body.appendChild(script);
});

window.styled = styled;
window.React = React;

window.App = {
    env: process.env,
    components: { Button, Link, Loader, MenuItem, Modal },
    util: { loadScript, getStorageValue },
    hooks: { useLocalStorage },
};

window.PluginRegistry = {};

const loadPlugins = async (urls) => {
    const dc = Date.now();
    const arr = [];
    for (let url of urls) {
        arr.push(loadScript(url + '?_dc=' + dc));
    }

    try {
        await Promise.allSettled(arr);
    } catch {}

    return Object.values(window.PluginRegistry).map(module => module.default);
};

export default loadPlugins(getStorageValue('plugins', '').split('\n').filter(i => i)).then(plugins => {
    for (let p of plugins) {
        plugin.register(p);
    }
});

