import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import LogoSvg from '../svg/logo.svg';
import PersonSvg from '../svg/person.svg';

const LinkWrapper = styled(Link)`
  width: 100%;
  height: ${props => ('true' === props.mobile ? '8rem' : '18rem')};
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  margin-bottom: 2rem;
`;

const LogoWrapper = styled.img`
  max-width: ${props => ('true' === props.mobile ? '40%' : '75%')};
`;

const Logo = ({ mobile = false, onClick = () => {} }) => {
  let link = '/bookmarks';
  let Svg = LogoSvg;
  if ('/bookmarks' === window.location.pathname) {
    link = '/discover/Popular';
     Svg = PersonSvg;
  }

  return (
    <LinkWrapper to={process.env.PUBLIC_URL + link} mobile={mobile ? 'true' : 'false'} onClick={onClick}>
      <LogoWrapper src={Svg} mobile={mobile ? 'true' : 'false'} />
    </LinkWrapper>
  );
};

export default Logo;
